.footer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
/* display: flex;: Biến phần tử thành một flex container, cho phép sắp xếp các phần tử con của nó theo hàng hoặc cột linh hoạt.
flex-direction: column;: Sắp xếp các phần tử con theo chiều dọc, tức là từ trên xuống dưới.
justify-content: center;: Căn giữa các phần tử con theo chiều dọc, tức là theo trục chính của flex container.
align-items: center;: Căn giữa các phần tử con theo chiều ngang, tức là theo trục chéo của flex container.
gap: 50px;: Tạo khoảng cách 50px giữa các phần tử con. */
.footer-logo{
  display: flex;
  align-items: center;
  gap: 20px;
}

.footer-logo p{
  color:#383838;
  font-size: 46px;
  font-weight: 700;
}

.footer-link{
  display: flex;
  list-style: none;
  gap:50px;
  color:#252525;
  font-size: 20px;
}

.footer-link li{
  cursor: pointer;
}

.footer-social-icon{
  display: flex;
  gap:20px;

}

.footer-icons-container{
  padding: 10px;
  padding-bottom: 6px;
  background: #fbfbfb;
  border: 1px solid #ebebeb;
}

.footer-copyright{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap:30px;
  width: 100%;
  margin-bottom: 30px;
  color:#1a1a1a;
  font-size: 20px;
}

.footer-copyright hr{
  width: 80%;
  border:none;
  border-radius: 10px;
  height: 3px;
  background: #c7c7c7;
}

@media(max-width:800px){
  .footer{
    gap:20px;
  }
  .footer-logo{
    gap:10px;
    align-items: end;
  }
  .footer-logo img{
    width: 40px;
  }
  .footer-logo p{
    font-size: 25px;
  }
  .footer-link{
    font-size: 14px;
    gap: 10px;
    padding-left: 0;
  }
  .footer-social-icon img{
    width: 22px;
  }
  .footer-copyright{
    font-size: 13px;
  }
}